.card {
  position: relative;
  border-radius: 24px;

  &__header {
    padding-top: 161px;
    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 23px;
    /* identical to box height */

    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: #ffffff;
    @media screen and (min-width: 768px) {
      font-family: Titan One;
      font-size: 20px;
      line-height: 23px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: #ffffff;
    }
    @media screen and (min-width: 1280px) {
      font-family: Titan One;
      font-size: 30px;
      line-height: 34px;
      text-align: center;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: #ffffff;
    }
  }
  &__label {
    width: 200px;
    margin: 66px auto;
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 152%;
    /* or 21px */

    text-align: center;
    letter-spacing: 0.04em;
    text-transform: none;
    color: #ffffff;
    @media screen and (min-width: 768px) {
      font-family: DM Sans;
      font-weight: 700;
      font-size: 14px;
      line-height: 176%;
      letter-spacing: 0.04em;
      color: #ffffff;
    }
    @media screen and (min-width: 1280px) {
      font-family: DM Sans;
      font-weight:700;
      font-size: 16px;
      line-height: 176%;
      letter-spacing: 0.04em;

      color: #ffffff;
    }
  }

  &__dots {
    position: absolute;
    width: 26px;
    top: 214px;
    left: 127px;
    @media screen and (min-width: 768px) {
      left: 97px;
    }
    @media screen and (min-width: 1280px) {
      left: 151px;
    }
  }
  &__arrow {
    position: absolute;
    left: 120px;
    bottom: 41px;
    @media screen and (min-width: 768px) {
      left: 90px;
    }
    @media screen and (min-width: 1280px) {
      left: 144px;
    }
  }
  &__image {
    position: absolute;
    top: -113px;
    right: 15px;
    @media screen and (min-width: 768px) {
      right: 0;
    }
    @media screen and (min-width: 1280px) {
      right: 0;
      top: -250px;
      width: 337px;
      height: 335px;
    }
  }

  @media screen and (max-width: 767px) {
    width: 280px;
    height: 485px;
  }
  @media screen and (min-width: 768px) {
    width: 220px;
    height: 485px;
  }
  @media screen and (min-width: 1280px) {
    width: 329px;
    height: 525px;
  }
  &--icecream {
    background-color: #ffb8ca;
  }
  &--coffee {
    background-color: #f0d1a5;
  }
  &--milkshakes {
    background-color: #c2e297;
  }
}
